import React from 'react'
import _ from 'lodash'
import './Windows.css'
import Window_open from './MS_Window.png'
import socketIOClient from 'socket.io-client'
const socket = socketIOClient()

const WindowsOpen = () => {
   const [windowsList, setWindowsList] = React.useState([])    // http: --> ROOM_WIN
   const [gpioWindows, setGpioWindows] = React.useState('')

   React.useEffect(() => {
      fetch('ROOM_WIN')
         .then(response => response.json())
         .then(data => {
            let res = _.filter(data, {state: 1})      // select on-line items { state:1 }
            console.log('FILT: '+ JSON.stringify(res))
            res = _.sortBy(res,['order'])             // sort by 'order'
            console.log('SORT: '+ JSON.stringify(res))
            setWindowsList(res)
         })
   }, [])

   // res.forEach(function (item, index) { console.log(item._id, index, item) })
   // WX:99 W1:0 W2:1 W3:1 W4:0 W5:0
   // console.log( _.pick(gpioWindows,ย['W1',ย'W2',ย'W3']))
   // console.log(_.includes(gpioWindows,ย1))      // == true
   // res = _.slice(windowsList,0,3)       // display max --> 4 windows
   // console.log('MAX: '+ JSON.stringify(res))

   React.useEffect(() => {
      socket.on('GpioBank', data => {
         let GPIOS = data.substr(data.search('W'),42)    // W1:0 W2:1 W3:1 W4:0 W5:0
         // console.log(GPIOS)
         GPIOS = GPIOS.replace('W1:0 ','')
            .replace('W2:0 ','')
            .replace('W3:0 ','')
            .replace('W4:0 ','')
            .replace('W5:0','').trim()
         setGpioWindows(GPIOS)
         console.log(GPIOS)
      })
   }, [ gpioWindows ])

   return(
      <div>
         <div className='windows-container'>
            <ul>
               {windowsList.map(item =>
                  <li className='window-li' key={item._id}>
                     <img className='window-icon' src={Window_open} alt='!'/>
                     <div className='window-name'>{item.win}</div>
                  </li>)
               }
            </ul>
         </div>
         <div className="motion_PIR">
            Gpio: { gpioWindows }
         </div>
      </div>
   )
}
export default WindowsOpen



