import React from "react"
import socketIOClient from "socket.io-client"
const socket = socketIOClient()

const MotionPIR = ( ) => {
   const { pir_item } = useSocket('GetGpioBank')
   return (
      <div className="motion_PIR">
         MOTION: {pir_item}
      </div>
   )
}
export default MotionPIR


const useSocket = ( topic ) => {
   const [pir, setPir] = React.useState('')
   const [pir_item, setPiritem] = React.useState('')
   socket.emit(topic)  // request streaming PIRs
   React.useEffect(() => {
      socket.on('GpioBank', data => {
         setPir(data)
         setPiritem(data.substr(data.search('PIR'),5))
      })
   }, [ socket ])
   return { pir, pir_item }
}
