import React, {Component} from 'react'
import socketIOClient from "socket.io-client"

const socket = socketIOClient()

class Backlight extends Component {
   constructor(props) {
      super(props)
      this.state = { response: null }
   }
   componentDidMount() {
      socket.on("BackLight_API", data => { this.setState({ response: data }) })  // Backlight level
      socket.emit('Get_BackLight', { Get_BackLight : 'request' })  // request Backlight
   }

   render() {
      // const { response } = this.state
      return (
         <div className="backlight">
            <div> Backlight: { this.state.response}</div>
         </div>
      )
   }
}
export default Backlight

